body {
  font-family: "Courier New", Courier, monospace;
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}
/* DrawerNav */
/* responsive */

@media screen and (min-width: 781px) {
  .open::after,
  .open,
  .open::before,
  .menu {
    display: none;
  }
}
/* to here */

ul {
  list-style: none;
}

/* ++++ Tglbutton ++++ */

#navTgl {
  display: none;
}
label {
  cursor: pointer;
  position: fixed;
  top: 0;
  right: 0;
}
.open {
  z-index: 2;
  width: 60px;
  height: 48px;
  transition: background 0.5s,
    transform 0.5s cubic-bezier(0.76, 0.52, 0.29, 1.25);
}
.open::before,
.open::after {
  content: "";
}
.open span,
.open::before,
.open::after {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  left: 30%;
  width: 40%;
  border-bottom: 2px solid white;
  transition: transform 0.5s cubic-bezier(0.76, 0.52, 0.29, 1.25);
}
.open span,
.open::before,
.open::after:hover {
  color: "rgba(255,255,255,0.7)";
}

.open::before {
  transform: translateY(-8px);
}
.open::after {
  transform: translateY(8px);
}
.close {
  z-index: 1;
  width: 100%;
  height: 100%;
  pointer-events: none;
  transition: background 0.5s;
}

#navTgl:checked + .open span {
  transform: scaleX(0);
}

#navTgl:checked + .open::before {
  transform: rotate(45deg);
}
#navTgl:checked + .open::after {
  transform: rotate(-45deg);
}
#navTgl:checked ~ .close {
  background: rgba(0, 0, 0, 0.7);
}

/* ++++ drawermenu ++++ */
.menu {
  z-index: 1;
  position: fixed;
  overflow: auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 6%;
  margin: 0;
  box-sizing: border-box;
  transform: translateX(-100%);
  transition: transform 0.5s cubic-bezier(0.33, 1.01, 0.33, 0.97);
}

.menu a {
  cursor: pointer;
  font-size: 20px;
  color: white;
}

.menu h2 {
  text-align: center;
}
.menu ul {
  padding: 0;
  text-align: center;
  width: 250px;
  margin: 0 auto;
}
.menu li {
  font-size: 0.9em;
  line-height: 1.4;
}
.menu a {
  display: block;
  padding: 1.4em 2em;
  text-decoration: inherit;
  transition: background 0.5s;
}

.menu li:hover:after {
  display: block;
  content: "";
  width: 100px;
  margin: 0 auto;
  border-bottom: 1px solid #fff;
}
#navTgl:checked ~ .menu {
  transform: none;
}

/* DrawerNavここまで */

/* Main */
/* PROFILE */
.main {
  color: white;
  height: auto;
  background-color: rgba(0, 0, 0, 0.9);
}
